import React, { useState, useEffect, useMemo, useCallback, useRef } from 'react';
import './App.css';
import OptimizedCarousel from './components/OptimizedCarousel';
import CategoryTabs from './components/categoriesTab';

// Style constants to avoid recreating on every render
const FULL_SCREEN_STYLES = {
  width: '100vw',
  height: '100vh',
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center',
  backgroundColor: '#0d0716',
};

const LOADING_STYLES = {
  ...FULL_SCREEN_STYLES,
  color: '#fff',
  fontSize: '24px',
  zIndex: 1,
  flexDirection: 'column',
  gap: '16px',
};

const ERROR_STYLES = {
  ...FULL_SCREEN_STYLES,
  color: '#ff4444',
  fontSize: '18px',
  padding: '20px',
  textAlign: 'center',
  zIndex: 1,
};

const NO_REELS_STYLES = {
  ...FULL_SCREEN_STYLES,
  color: '#fff',
  fontSize: '18px',
  zIndex: 1,
};

const CONTAINER_STYLES = {
  width: '100vw',
  height: '100vh',
  overflow: 'hidden',
  margin: 0,
  padding: 0,
  top: 0,
  left: 0,
  position: 'fixed',
  zIndex: 1,
};

const SPINNER_STYLES = {
  width: '40px',
  height: '40px',
  border: '3px solid rgba(255, 255, 255, 0.1)',
  borderTopColor: '#ff9f1c',
  borderRadius: '50%',
  animation: 'spin 0.8s linear infinite',
};

const authToken = `eyJhbGciOiJSUzI1NiIsInR5cCI6IkpXVCJ9..FgSj0W-kVNfDw8we6LE3Z8i-8wG9EUBQcg1kjXyvY1QIzbfkx3QJJ3_lRMH_bpD2gQ6zEBzWhc1N0S73DGLxLJLXLPq2wiXEJPLkAYNaQkHtxjZSc8JoYlweYO0zYqjeYkAjYt--PtI744eiSQBr-_iQXtIZMG-ZYJJQXJz4sl2okovQYFRzJWAueVVXPByJxguV6SQ1Saz72E56_Rb7TAx81QSA1A36yrOAmT3gwsGgfBo-FFOfZfwVffR_4oKlJXpJZD5mnE_J9buzk7qvMcKnA3YhbaIuB_vDZH3sERHGhAqAHlDH3wEuTB2-3cYaZF_r7G7TjzRmrlDPZzeyxg`;

// Data cache for category switching
const dataCache = new Map();

// Loading spinner component
const LoadingSpinner = () => (
  <div style={LOADING_STYLES}>
    <style>
      {`@keyframes spin { to { transform: rotate(360deg); } }`}
    </style>
    <div style={SPINNER_STYLES} />
    <span>Loading reels...</span>
  </div>
);

function App() {
  const [data, setData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [category, setCategory] = useState('Stories');
  const [windowHeight, setWindowHeight] = useState(() => typeof window !== 'undefined' ? window.innerHeight : 300);
  const [isTransitioning, setIsTransitioning] = useState(false);
  const abortControllerRef = useRef(null);

  // Handle window resize for baseWidth calculation
  useEffect(() => {
    const handleResize = () => {
      setWindowHeight(window.innerHeight);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  // Prefetch other categories in background
  useEffect(() => {
    const prefetchCategories = ['Stories', 'Pravachan', 'Bhajan', 'Darshan'];
    
    const prefetch = async (cat) => {
      if (dataCache.has(cat)) return;
      
      try {
        const response = await fetch(
          `https://devgateway.techxrdev.in/api/content/content/reels/feed?category=${cat}&page=1&limit=50&userId=12`,
          {
            headers: {
              'Authorization': `Bearer ${authToken}`
            }
          }
        );
        
        if (response.ok) {
          const result = await response.json();
          dataCache.set(cat, result);
        }
      } catch (err) {
        // Silent fail for prefetch
      }
    };

    // Prefetch after initial load with delay
    const timer = setTimeout(() => {
      prefetchCategories.forEach((cat, index) => {
        setTimeout(() => prefetch(cat), index * 1000);
      });
    }, 2000);

    return () => clearTimeout(timer);
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      // Check cache first
      if (dataCache.has(category)) {
        setData(dataCache.get(category));
        setLoading(false);
        setError(null);
        return;
      }

      // Abort previous request if any
      if (abortControllerRef.current) {
        abortControllerRef.current.abort();
      }
      
      abortControllerRef.current = new AbortController();
      
      try {
        setLoading(true);
        setIsTransitioning(true);
        
        const response = await fetch(
          `https://devgateway.techxrdev.in/api/content/content/reels/feed?category=${category}&page=1&limit=50&userId=12`,
          {
            headers: {
              'Authorization': `Bearer ${authToken}`
            },
            signal: abortControllerRef.current.signal
          }
        );
        
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const result = await response.json();
        
        // Cache the result
        dataCache.set(category, result);
        
        setData(result);
        setError(null);
      } catch (err) {
        if (err.name === 'AbortError') return;
        setError(err.message);
        setData(null);
        console.error('Error fetching reels:', err);
      } finally {
        setLoading(false);
        setIsTransitioning(false);
      }
    };

    fetchData();
    
    return () => {
      if (abortControllerRef.current) {
        abortControllerRef.current.abort();
      }
    };
  }, [category]);

  // Extract videoUrl array from API response - memoized to avoid recalculation
  const videoUrls = useMemo(() => {
    return data?.reels?.map(reel => reel.videoUrl) || [];
  }, [data]);

  // Handle category change - memoized with useCallback
  const handleCategoryChange = useCallback((newCategory) => {
    setCategory(newCategory);
  }, []);

  // Show loading state
  if (loading) {
    return <LoadingSpinner />;
  }

  // Show error state
  if (error) {
    return (
      <div style={ERROR_STYLES}>
        Error loading reels: {error}
      </div>
    );
  }

  // Show message if no reels found
  if (!videoUrls.length) {
    return (
      <div style={NO_REELS_STYLES}>
        No reels found for {category}
      </div>
    );
  }

  return (
    <div style={CONTAINER_STYLES}>
      <CategoryTabs 
        category={category} 
        onChange={handleCategoryChange}
      />
      <OptimizedCarousel 
        key={category}
        baseWidth={windowHeight}
        items={videoUrls}
      />
    </div>
  );
}

export default App;
